.Item {
  width: 100%;
  height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  border: 1px solid black;
}

.Item-img {
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.Item-text-wrap {
  flex: 2;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.Item-name {
  flex: 3;
  height: 100%;
}
.Item-price {
  flex: 2;
  height: 100%;
}

.Item-etc {
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.Item-up-btn {
  flex: 1;
}

.Item-amount {
  flex: 2;
  text-align: center;
}

.Item-down-btn {
  flex: 1;
}

.Item-delete-btn {
  flex: 1;
}
