.Tab {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-top: 1px solid #e4e8ff;
}

.Tab-link {
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.Tab-link:not(:last-child) {
  border-right: 1px solid #e4e8ff;
}
