.Home {
  width: 100%;
  height: 100%;
  background-color: #f7f7ff;

  display: flex;
  flex-direction: column;
}

.Home-logo-wrapper {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

  .Home-logo {
    width: 100vmin;

    max-width: 100%;
    max-height: 100%;
  }

.Home-btn-wrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

  .Home-btn {
    width: 60vw;
    height: 10vw;
    background-color: #ffffff;
    border: 3px solid #dcdeff;
    border-radius: 2vw;

    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;
  }


@media screen and (min-width: 567px){
  .Home{
    flex-direction: row;
  }

  .Home-logo {
    height: 100vmin;

    max-width: 100%;
    max-height: 100%;
  }

  .Home-btn-wrapper {
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }

    .Home-btn {
      width: 25vw;
      height: 5vw;
      border: 3px solid #dcdeff;
    }
}